import { useRef, useState, useEffect, useContext } from "react";
import { useRecoilState } from "recoil";
import { conversationState } from "../../store/atoms";
import SendIcon from '@mui/icons-material/Send';
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import StopIcon from '@mui/icons-material/Stop';
import axios from "axios";
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import { MyContext } from "../../Context/MyContext";


const PromptBar = () => {
  const [conversation, setConversation] = useRecoilState(conversationState);
  const [inputQuestion, setInputQuestion] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [localTranscript, setLocalTranscript] = useState("");
  const inputRef = useRef(null);
  const { value, setValue } = useContext(MyContext);

  const {
    transcript,
  } = useSpeechRecognition();

  const apiUrl = "https://nhbli32qjd.execute-api.us-east-1.amazonaws.com/llama-test/teating";

  const handleInputChange = (e) => {
    setInputQuestion(e.target.value);
  };

  const generateAnswer = async (prompt) => {
    const postData = { user_prompt: prompt };

    try {
      const response = await axios.post(apiUrl, postData);
      const answer = response.data;
      return answer;
    } catch (error) {
      console.error("Error:", error);
      throw error;
    }
  };

  const handleSubmit = async () => {
    const prompt = inputQuestion || localTranscript || value; 
    if (!prompt) {
      alert("Please add a prompt to continue!");
      return;
    }

    setIsLoading(true);
    try {
      let answer = await generateAnswer(prompt);

      const newConversation = {
        id: new Date().getTime(),
        date: new Date(),
        question: prompt,
        response: answer,
      };

      const updatedConversations = [newConversation, ...conversation];

      setConversation(updatedConversations);
      setInputQuestion("");
      setLocalTranscript("");
      inputRef.current.focus();
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSubmit();
    }
  };

  const handleStartListening = () => {
    setLocalTranscript(""); 
    setInputQuestion(""); 
    SpeechRecognition.startListening();
  };

  useEffect(() => {
    if (transcript) {
      setLocalTranscript(transcript);
      setInputQuestion(transcript);
    }else if(value){
      setLocalTranscript(value);
      setInputQuestion(value);
      handleSubmit();
    }
  }, [transcript, value]);

  return (
    <div className="flex items-center p-3 bg-gray-100 rounded-full container w-full max-w-xl" style={{ marginTop: "1rem" }}>
      <input
        ref={inputRef}
        type="text"
        placeholder="Enter a prompt here"
        className="flex-grow bg-transparent outline-none px-4 py-2 text-gray-700"
        value={inputQuestion}
        onChange={handleInputChange}
        onKeyPress={handleKeyPress}
      />
      <button onClick={handleSubmit} className="flex items-center justify-center w-8 h-8 text-gray-400 hover:text-gray-600">
        <SendIcon />
      </button>
      <button onClick={handleStartListening} className="flex items-center justify-center w-8 h-8 text-gray-400 hover:text-gray-600">
        <KeyboardVoiceIcon/>
      </button>
      <button onClick={SpeechRecognition.stopListening} className="flex items-center justify-center w-8 h-8 text-gray-400 hover:text-gray-600">
        <StopIcon/>
      </button>
      {isLoading && (
        <div className="ml-4">
          <Skeleton height={20} width={200} />
        </div>
      )}
    </div>
  );
};

export default PromptBar;
