import React, { useContext, useState } from 'react'
import { CgProfile } from 'react-icons/cg'

import "./Header.css"
import { MyContext } from '../../Context/MyContext';

export default function Header() {
	const { isDarkTheme } = useContext(MyContext);
	const themeStyles = {
		backgroundColor: isDarkTheme ? '#333' : '#FFF',
		color: isDarkTheme ? '#FFF' : '#000',
	  };
	const [showDropdown, setShowDropdown] = useState(false)
	

	const toggleDropdown = () => {
		setShowDropdown(!showDropdown)
	}

	return (
		<div  style={isDarkTheme ? themeStyles : {}} className="bg-white w-full flex flex-wrap justify-around">		
			<div className="flex items-center w-full py-3 justify-between px-5 h14">
                <div className="text-xl justify-left">
                    Proptech AI
                </div>
				<div>
					
						<div
							className="flex flex-wrap items-center justify-center cursor-pointer"
							onClick={toggleDropdown}
						>
							<div className="py-2 text-center">
								<CgProfile className="text-3xl" />
							</div>
							{/* <span className="w-4/6 pl-1 text-md font-semibold text-gray-700 tracking-wide text-center ">
								{'User'}
							</span> */}
							{/* <div className=" w-1/6 py-3 ">
								<RiArrowDownSLine
									onClick={toggleDropdown}
									className="text-gray-500 text-xl cursor-pointer"
								/>
							</div> */}
						</div>
				
					{showDropdown && (
						<div className="absolute bg-white border rounded-lg py-2 px-4 text-md w-48 divide-y">
							<div
								className=" py-1 cursor-pointer hover:text-blue-600 w-full font-semibold text-gray-600"
							>
								Logout
							</div>
						</div>
					)}
				</div>
			</div>
		</div>
	)
}
