import React, { useContext, useState } from 'react';
import DehazeIcon from '@mui/icons-material/Dehaze';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import ReplayIcon from '@mui/icons-material/Replay';
import { useRecoilState } from 'recoil';
import { conversationState, pastConversationsState } from "../../store/atoms";
import ThemeToggleButton from './ThemeToggleButton';
import { MyContext } from '../../Context/MyContext';

const Sidebar = () => {
  const { isDarkTheme } = useContext(MyContext);
  const themeStyles = {
      backgroundColor: '#333',
    };

  const [isOpen, setIsOpen] = useState(true); // State to manage sidebar visibility
  const [pastConversations, setPastConversations] = useRecoilState(pastConversationsState);
  const [conversation, setConversation] = useRecoilState(conversationState);

  const handleClearAll = () => {
    setPastConversations([]);
    setConversation([]);
    window.location.reload();
  };

  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div style={isDarkTheme? themeStyles : {}} className={`flex flex-col top-0 left-0 h-full bg-white border-r transition-width duration-300 h-auto ${isOpen ? 'w-64' : 'w-16'}`}>
      <div className="flex items-center h-14 p-4">
        <DehazeIcon onClick={toggleSidebar} className="cursor-pointer" />
      </div>
      {isOpen && (
        <div className="flex items-center h-14 p-4">
          <button
            className="w-[50%] h-[40px] rounded-3xl bg-gray-600 text-center p-2 text-[#ffffff] font-bold"
            onClick={handleClearAll}
          >
            Clear
          </button>
        </div>
      )}
      <div className="overflow-y-auto overflow-x-hidden flex-grow">
        <ul className="flex flex-col py-4 space-y-2" >
          {isOpen && (
            <li className="px-5">
              <div className="flex flex-row items-center h-8 mt-6">
                <div className={`text-sm font-light tracking-wide text-gray-500 ${isDarkTheme ? 'hover-bg-black' : ''}`} style={isDarkTheme? {color: "#fff"} : {}}>Recent</div>
              </div>
            </li>
          )}
          <li className="mt-72">
            <a href="#" className={`relative flex flex-row items-center h-11 focus:outline-none hover:bg-gray text-gray-600 hover:text-black border-l-4 border-transparent hover:border-indigo-500 pr-6 ${isDarkTheme ? 'hover-bg-black' : ''}`} style={isDarkTheme? {color: "#fff"} : {}}>
              <span className="inline-flex justify-center items-center ml-4">
                <HelpOutlineIcon />
              </span>
              {isOpen && <span className="ml-2 text-sm tracking-wide truncate">Help</span>}
            </a>
          </li>
          <li>
            <a href="#" className={`relative flex flex-row items-center h-11 focus:outline-none hover:bg-gray-50 text-gray-600 hover:text-gray-800 border-l-4 border-transparent hover:border-indigo-500 pr-6 ${isDarkTheme ? 'hover-bg-black' : ''}`} style={isDarkTheme? {color: "#fff"} : {}}>
              <span className="inline-flex justify-center items-center ml-4">
                <ReplayIcon />
              </span>
              {isOpen && <span className="ml-2 text-sm tracking-wide truncate">Activity</span>}
            </a>
          </li>
          <li>
            <a href="#" className={`relative flex flex-row items-center h-11 focus:outline-none hover:bg-gray-50 text-gray-600 hover:text-gray-800 border-l-4 border-transparent hover:border-indigo-500 pr-6 ${isDarkTheme ? 'hover-bg-black' : ''}`} >
              <span className="inline-flex justify-center items-center ml-4">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z"></path>
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path>
                </svg>
              </span>
              {isOpen && <span className={`ml-2 text-sm tracking-wide truncate`}>Settings</span>}
            </a>
          </li>
        </ul>
        {isOpen && (
        <div className="flex items-center h-14 p-4">
          {/* <button
            className="w-[100%] h-[40px] rounded-3xl bg-gray-600 text-center p-2 text-[#ffffff] font-bold mt-96"
            // onClick={handleClearAll}
          >
            Toggle Theme
          </button> */}
           <ThemeToggleButton />

        </div>
      )}
      </div>
    </div>
  );
};

export default Sidebar;
