import React from 'react'
import CustomCard from './CustomCard'
import DrawIcon from '@mui/icons-material/Draw';
import CodeIcon from '@mui/icons-material/Code';
import LightbulbCircleIcon from '@mui/icons-material/LightbulbCircle';

const InitialPage = () => {
  return (
    <> 
    <div className='text-start container'>
      <h1 className='bg-color text-2xl pl-1 m-0'>Hello, User</h1>
      <h1 className='heading text-2xl pl-1 mb-12'>How can I help you today?</h1>
    </div>
    <div className="cards overflow-x-auto whitespace-nowrap hide-scrollbar container gap-4">
      <CustomCard title="What is PropTech?" icon={<DrawIcon />} />
      <CustomCard title="How does blockchain impact real estate?" icon={<CodeIcon />} />
      <CustomCard title="What are the benefits of smart buildings?" icon={<LightbulbCircleIcon />} />
      <CustomCard title="What are lifestyle travel awards?" icon={<DrawIcon />} />
    </div>
    </>
  )
}

export default InitialPage