import { useRecoilValue } from "recoil";
import { conversationState } from "../../store/atoms";
import { Fragment } from "react";
import ConversationCard from "./ConversationCard";

const CurrentCoversation = () => {
  const conversation = useRecoilValue(conversationState);

  return (
  <div className="h-[74vh] overflow-auto">
  <div className="w-full flex flex-col-reverse justify-end items-center gap-4 p-3">
      {conversation.map((chat) => {
        return (
          <Fragment key={chat.id}>
            <ConversationCard
              chat={chat}
              isResponse={true}
              isReadOnly={false}
            />
    
            <ConversationCard
              chat={chat}
              isResponse={false}
              isReadOnly={false}
            />
          </Fragment>
        );
      })}
    </div>
  </div>  
);
};

export default CurrentCoversation;
