import React, { useContext } from 'react';
import { MyContext } from '../../Context/MyContext';

const CustomCard = ({ title, icon }) => {
  const { value, setValue, isDarkTheme } = useContext(MyContext);

  return (
    <div onClick={() => setValue(title)} className="bg-gray-100 p-4 rounded-lg shadow-md flex flex-col justify-between h-60 w-60 space-x-2 inline-block overflow-hidden" style={{marginBottom: "5rem", cursor:"pointer"}}>
      <p className="text-gray-800 overflow-hidden text-ellipsis font-bold text-lg whitespace-normal">{title}</p>
      <div className="flex justify-end items-end mt-auto">
        <div className="bg-white rounded-full shadow" style={isDarkTheme? {color: "black"} : {}}>{icon}</div>
      </div>
    </div>
  );
};

export default CustomCard;
